.app {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .calculator {
    border: 2px solid #333;
    border-radius: 5px;
    width: 300px;
    background-color: #747474;
    color: #fff;
    text-align: right;
  }
  
  .display {
    font-size: 24px;
    padding: 10px;
  }
  
  .buttons {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
  
  button {
    font-size: 24px;
    border: none;
    padding: 10px;
    cursor: pointer;
    background-color: #454444;
    color: #3c3c3c;
  }
  
  button:hover {
    background-color: #6f6f6f;
  }
  