.calculator-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 300vh;
    background-color: #e1e1e1; /* Change to blue color, you can use any other shade of blue you prefer */
}

  
  .calculator {
    border: 8px solid #030305;
    padding: 20px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.8); /* Reduced shadow for better readability */
    border-radius: 25px;
    background-color: rgb(28, 28, 28); /* Darker background color for a sleek look */
    max-width: 800px;
    width: 100%;
    text-align: center;
    margin: 0 auto;
  }
  
  .display {
    font-size: 24px;
    text-align: right;
    margin-bottom: 10px; /* Reduced margin for better spacing */
    color: #fff; /* Text color for better contrast */
    border: 1px solid #030305; /* Add a border */
    padding: 10px; /* Add padding */
    border-radius: 5px; /* Rounded corners */
  }
  
  .buttons {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
    padding: 10px; /* Added padding for better button separation */
    border-radius: 5px; /* Rounded corners */
}

button {
    padding: 15px; /* Increased padding for larger buttons */
    font-size: 20px;
    border: none; /* Remove the border property */
    background-color: #0a0931;
    color: #fff; /* Button text color for better contrast */
    cursor: pointer;
    border-radius: 5px; /* Slightly rounded buttons */
    transition: background-color 0.3s; /* Smooth hover effect */
}

  
  button:hover {
    background-color: #555; /* Darker background on hover */
  }
  